<template>
  <base-section id="about-us-details">
    <v-img
      :min-height="minHeight"
      :src="require('@/assets/bkground-2.jpg')"
      class="white--text"
      gradient="to right, rgba(5, 11, 31, .8), rgba(5, 11, 31, .8)"
    >
      <base-section-heading title="MORE ON DRAC" />

      <div>
        <v-container>
          <v-row>
            <v-col
              v-for="(feature, i) in features"
              :key="i"
              cols="12"
              md="6"
            >
              <div
                v-bind="feature"
                horizontal
              >
                <h3> {{ feature.title }} </h3>
                <p> {{ feature.content }} </p>
              </div>

              <div class="imgSlot">
                <v-img
                  :min-height="minHeight"
                  :src="feature.image"
                  class="white--text"
                />
              </div>
            </v-col>
          </v-row>
        </v-container>
      </div>
    </v-img>
  </base-section>
</template>

<script>
  export default {
    name: 'SectionAboutUsDetails',

    data: () => ({
      minHeight: 0,
      features: [
        {
          title: 'Our Objective',
          icon: 'mdi-fountain-pen-tip',
          content: 'Provide accurate, relevant and practical disability-related information and statistics as background and justification for evidence-informed interventions as well as opportunities for information sharing among organisations that provide services to PWDs in Nigeria. Raise awareness on the health and welfare needs of PWDs and WWDs in Nigeria with a view to changing disability stereotypes and dispelling myths and misconceptions related to disability and mobilising practical efforts that make the Nigerian environment more responsive and enabling for PWDs.Advocate at national and international levels for policies and actions, which further the human rights of PWDs and WWDs. Maintain consultative status on Disability Accessibility issues within Nigeria and Africa by developing and maintaining links between PWDs service providers and relevant agencies. Facilitate opportunities for the voices of WWDs to be heard in national and international fora in which ideas about disability are exchanged and ensure that the disability agenda is kept on the front burner of National and International discourse and ensure the meaningful involvement of women with disabilities in the wider women sector. Urge Government, Private Sector and International Development Agencies to channel significant amounts of the resources available for health and development to interventions for PWDs and WWDs. Initiate and mainstream the development of effective STD/HIV prevention, reproductive health, gender and social support services for women with disabilities through advocacy for conditions that are more favourable to their good health and social wellbeing. Support employers seeking to become more disability friendly, and state governments engaged in policy reform favourable to the disability agenda. Strengthen the organizational and management capacity of Organizations of Persons with Disabilities (OPDs) in order to increase their effectiveness and impact.',
        },
        {
          image: require('@/assets/drac-4.jpg'),
        },
        {
          image: require('@/assets/drac-7.jpg'),
        },
        {
          title: 'Core Values',
          icon: 'mdi-image-size-select-actual',
          content: 'Transparency and Accountability… Upholding the principles of good governance and responsibility to stakeholders and Community is integral to our existence. Inclusion and Participation… Our programs/actions for PWDs are built on the premise of ‘NOTHING ABOUT US, WITHOUT US’. DRAC exists to ensure greater and meaningful involvement of PWDs in all spheres of life. Dignity and Equity. We strive to maintain the dignity of PWDs while fighting for justice and equality for persons with disabilities. DRAC demonstrates zero tolerance for any policy/practice that undermines the dignity and/or equitable treatment of PWDs. Selfless Service to Humanity… Everything DRAC does is with a view to improving the wellbeing of Persons with Disabilities. DRAC promotes no selfish agenda and is focused purely on the DISABILITY INCLUSION.',
        },
        {
          title: 'Our Program Areas',
          icon: 'mdi-fountain-pen-tip',
          content: 'To achieve our objectives, we use multidimensional approaches and strategies that target the different points at which disability can be effectively addressed. These include: Research and Documentation (R & D) Policy and Advocacy (PA) Disability Rights Education (DRE) Disability Inclusive Service Delivery (DISD)',
        },
        {
          image: require('@/assets/drac-3.jpg'),
        },
        {
          image: require('@/assets/drac-7.jpg'),
        },
        {
          title: 'Our Activities',
          icon: 'mdi-image-size-select-actual',
          content: 'Services/Empowerment: Providing information and counselling about resources/services aimed at empowering PWDs to live independently and for building the capacity among caregivers in their care giving roles. Counselling services that address issues related to trauma, psychological stress, self-stigma and discrimination. Research, Surveillance and Documentation: Conducting qualitative and quantitative research aimed at understanding the different factors that impact the lives of PWDs. Research activities include conducting focus group discussions, key informant interviews, expert panel interviews, and surveys. Welfare: Providing food and clothing support to PWDs in need. Referral services for additional economic support will be provided. Workplace programming: Assisting employers in creating an enabling environment for hiring, training, and retraining of PWDs. Assistance is also given to help develop or improve workplace policies that are sensitive to the needs of PWDs. Audits: Carrying out accessibility audits on public facilities to identify barriers to services and providing consultation services for agencies in the public or private sector wishing to make their facilities disability friendly. Women in Leadership and Development: Organizing Annual Leadership retreat for Women with Disabilities. Media Engagement: Partnering with the media (traditional and new media) to increase awareness as well as development of quality sensitive IEC and SBC materials on disability issues. Capacity Building: Strengthening technical capacity of PWDs as well as Organisational Development Interventions for PWD-led NGOs and CBOs. Recreation: Developing creative strategies and opportunities that will encourage the participation of PWDs in social and physical activities.',
        },
      ],
      computed: {
        minHeight () {
          const height = this.$vuetify.breakpoint.mdAndUp ? '100vh' : '50vh'

          return `calc(${height} - ${this.$vuetify.application.top}px)`
        },
      },
    }),
  }
</script>

<style scoped>
#theme-features{
  padding-top: 20px;
}
</style>
